article,
aside,
details,
figcaption,
figure,
footer,
header,
nav,
section,
summary,
menu,
main {
    display: block;
}

audio,
canvas,
video,
progress {
    display: inline-block;
    vertical-align: middle;
}

audio:not([controls]),
[hidden],
template {
    display: none;
    height: 0;
}

* {
    box-sizing: inherit;
    padding: 0;
    margin: 0;
    font-size: inherit;
    line-height: inherit;
    &::after,
    &::before {
        box-sizing: inherit;
    }
}

:after,
:before {
    vertical-align: inherit;
    text-decoration: inherit;
}

:root {
    box-sizing: border-box;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    text-rendering: optimizeLegibility;
}

html {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-tap-highlight-color: transparent;
}

a {
    background-color: transparent;
    &:focus {
        outline: thin dotted;
    }
    &:active,
    &:hover {
        outline: 0;
    }
}

iframe,
img,
svg {
    vertical-align: middle;
}

iframe,
embed,
object {
    max-width: 100%;
}

abbr[title] {
    border-bottom: none;
    text-decoration: underline;
    text-decoration: underline dotted;
}

code,
kbd,
pre,
samp {
    font-family: monospace, monospace;
    font-size: 1em;
}

pre {
    white-space: pre;
    white-space: pre-wrap;
    word-wrap: break-word;
    overflow: auto;
    margin: 1em 0;
}

nav {
    ul,
    ol {
        list-style: none;
    }
}

img {
    border: 0;
    -ms-interpolation-mode: bicubic;
}

svg:not(:root) {
    overflow: hidden;
}

fieldset,
legend {
    border: 0;
}

fieldset {
    min-width: 0;
}

legend {
    white-space: normal;
    color: inherit;
    display: table;
    max-width: 100%;
    padding: 0;
}

select {
    cursor: pointer;
    &:-moz-focusring {
        color: transparent;
        text-shadow: 0 0 0 #000;
    }
}

button,
input {
    &[disabled] {
        cursor: default;
    }
    &::-moz-focus-inner {
        border: 0;
        padding: 0;
    }
}

button,
input,
select,
textarea {
    vertical-align: baseline;
    font: inherit;
    background: none;
    border-radius: 0;
    margin: 0;
}

button,
input,
select {
    overflow: visible;
}

input {
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    &::-moz-focus-inner {
        border: 0;
        padding: 0;
    }
    &[type='checkbox'],
    &[type='radio'] {
        cursor: pointer;
        padding: 0;
    }
    &[type='checkbox'] {
        -moz-appearance: checkbox;
        -webkit-appearance: checkbox;
    }
    &[type='radio'] {
        -moz-appearance: radio;
        -webkit-appearance: radio;
    }
    &[type='number'] {
        width: auto;
        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
            height: auto;
        }
    }
}

button,
input[type='button'],
input[type='reset'],
input[type='submit'] {
    appearance: button;
    -moz-appearance: button;
    -webkit-appearance: button;
    cursor: pointer;
}

textarea {
    vertical-align: top;
    overflow: auto;
    resize: vertical;
    -moz-appearance: none;
    -webkit-appearance: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
    table-layout: fixed;
}

progress {
    display: inline-block;
    vertical-align: baseline;
}

menu {
    margin: 1em 0;
    padding: 0 0 0 40px;
}

hr {
    box-sizing: content-box;
    height: 0;
    overflow: visible;
}

sup {
    position: relative;
    top: -0.5em;
    font-size: 75%;
    line-height: 0;
    vertical-align: baseline;
}
